
.article_wrap {
  padding: 40px;
}
.article_title {
  display: inline-block;
  color: #222;
  font-size: 34px;
  font-weight: 600;
  border-bottom: 1px solid white;
  cursor: pointer;
}
.article_title:hover {
  border-bottom: 1px solid #222;
}
.article_info {
  color: #999;
  font-size: 14px;
  padding-top: 8px;
}
.tag_margin {
  margin: 3px;
}
.article_gist {
  text-align: left;
  padding-top: 40px;
  padding-bottom: 40px;
  font-size: 16px;
}
.article_button {
  display: inline-block;
  padding: 3px 12px;
  border: 2px solid #222;
  color: #222;
  font-size: 14px;
  cursor: pointer;
}
.article_all:hover {
  color: white;
  background: #000;
  font-weight: 600;
}
.article_underline {
  height: 1px;
  width: 40px;
  background: #545455;
  margin: 80px auto 0;
}



